@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,400italic,300,300italic);

html,
body,
#container {
	height: 100%;
	width: 100%;
	font-family: 'Open Sans', sans-serif;
	color: #FFF;
	margin: 0;
	padding: 0;
	font-size: 1em;
	font-weight: 300;
}

.content {
	width: 100%;
}

.datetime {
	padding: 20px;
	background: RGBA(0, 0, 0, 0.7);
	margin-bottom: 10px;
	margin-top: 0;
	font-weight: 300;
	flex: 0 0 100%;
	align-self: flex-end;
	box-sizing: border-box;
}

.datetime__time {
	font-size: 2em;
}

.datetime__date {
	font-size: 1em;
}

.datetime__hours {
	margin: 2px;
}

.datetime__minutes {
	margin: 2px;
}

.datetime__seconds-ampm {
	display: inline-block;
	vertical-align: middle;
	margin: 10px;
}

.datetime__seconds {
	font-size: .5em;
	display: block;
	border-bottom: 5px solid;
}

.datetime__ampm {
	font-size: .5em;
	text-transform: uppercase;
	display: block;
}

.datetime__month {
	margin: 10px;
}

.datetime__day {
	margin: 10px;
	margin-right: 0;
}

.datetime__year {
	margin: 10px;
}

.image {
	background-repeat: repeat;
	min-height: 100%;
	background-size: cover;
}

.image-container {
	display: flex;
	height: 100%;
	align-items: center;
	text-align: center;
	flex-wrap: wrap;
	box-shadow: 0 -208px 300px -150px #000 inset;
}

.image-container__wrapper {
	flex: 0 0 100%;
}

.list {
	display: inline-block;
	position: relative;
	z-index: 10;
	padding: 40px;
	text-align: left;
	vertical-align: top;
	background: RGBA(0, 0, 0, 0.7);
	margin-bottom: 10px;
	max-width: 400px;
	width: 320px;
	box-sizing: border-box;
}

.list h2 {
	margin-top: 0;
	padding-top: 0;
	font-weight: 300;
}

.list-container {
	flex: 0 0 100%;
}

.list ul {
	list-style: none;
	padding: 0;
	padding-bottom: 20px;
	margin: 0;
	margin-bottom: 10px;
	border-bottom: 1px solid #FFF;
}

.list__add-input {
	border-radius: 5px;
	outline: none;
	border: 1px solid #000;
	padding: 5px;
	width: calc(100% - 70px);
}

.list__item__label {
	display: inline-block;
	max-width: calc(100% - 50px);
	margin-left: 10px;
}

.list__item input[type=checkbox] {
	margin-right: 10px;
	margin-top: 5px;
	vertical-align: top;
}

.list__button {
	background: transparent;
	outline: none;
	border-radius: 100%;
	height: 36px;
	width: 36px;
	border: none;
	margin: 0 5px;
	padding: 3px;
	text-transform: uppercase;
}

.list__button img {
	width: 30px;
	height: 30px;
	vertical-align: middle;
}

.list__button--remove {
	float: right;
	vertical-align: middle;
}

.weather {
	color: #FFF;
	padding: 20px;
	text-align: center;
	margin-bottom: 10px;
	min-height: 100px;
	display: inline-block;
	font-size: .5em;
	border-top: 1px solid #FFF;
}

.weather__temp,
.weather__day {
	vertical-align: middle;
}

.weather__icon {
	height: 50px;
	width: 50px;
	vertical-align: middle;
}

.weather__location {
	font-size: .75em;
	margin-top: 15px;
}

.weather--current {
	border-bottom: 1px solid #FFF;
	margin-bottom: 10px;
}

.weather--forecast__item {
	margin-bottom: 20px;
}

.quote {
	color: #FFF;
	padding: 20px;
	text-align: center;
	margin-top: 10px;
	text-shadow: 1px 1px 5px #000;
	flex: 0 0 100%;
	box-sizing: border-box;
	align-self: flex-end;
}

.quote__text {
	font-size: 1.5em;
	font-style: italic;
	margin-bottom: 5px;
}

@media (min-width: 500px) {

.datetime__time {
	font-size: 4em;
}

}

@media (min-width: 1200px) {

.image {
	height: 100%;
	width: 100%;
	background-size: cover;
	background-repeat: no-repeat;
}

.list:not(:last-child) {
	margin-right: 10px;
}

.weather {
	display: block;
}

.weather--current {
	display: inline-block;
	margin-right: 10px;
	margin-bottom: 0;
	border-bottom: 0;
}

.weather--forecast {
	display: inline-block;
	border-left: 1px solid #FFF;
	margin-left: 10px;
}

.weather--forecast__item {
	display: inline-block;
	margin: 0 10px;
}

}

